import { createStore } from 'vuex'
import VuexPersistence from "vuex-persist"


const vuexLocalStorage = new VuexPersistence({
  supportCircular: true,
  key: 'vuex',
  storage: window.sessionStorage,
  reducer: (state) => ({
    user: state.user,
    count: state.count,
  })
})


export default createStore({
  state () {
    return {
      count: 0,
      categories: [],
      modalConfirm: null,
      searchResults: [],
      searchIn: [],
      authenticated: false,
      login_path: "login",
      username: "",
      searching: false,
      day: true,
      user: {
        "id": null,
        "username": "guest",
        "token": "",
        "avatar": "/static/assets/gamer-esports-player-competition-activity-256.webp",
        "friends": [
          
        ],

      },

      //quiz game mind
      questions: [],
      
    }
  },
  
  mutations: {
    increment (state) {
      state.count += 110
    },
    setUser (state, data) {
      state.user.username = data.username;
      state.user.id = data.id;
      state.user.friends = data.friends;
      state.user.avatar = data.avatar;
    },
    logout (state) {
      state.user.username = "guest",
      state.user.id = null,
      state.user.friends =  [],
      state.user.avatar = "/static/assets/gamer-esports-player-competition-activity-256.webp";
      
      
    }
  },
  getters: {
    getUserStatus (state) {
      state.user.username === 'guest' ? state.login_path = "login" : state.login_path = "logout"
      return state.login_path
    },
    getUserName (state) {
      return state.user.username
    }
  },
  plugins: [vuexLocalStorage.plugin],
})